.content__table-matrix {

    .table__product {
        min-width: 1070px;
    } 

    .table__product-amount {
        width: 27px;
        margin-right: 15px;
    }

    .table__product-id {
        width: 120px;
        margin-right: 15px;
    }

    .table__product-count {
        width: 160px;
        margin-right: 15px;
    }

    .table__product-name {
        width: 420px;
        margin-right: 15px;
    }

    .table__product-actions {
        width: 80px;
        margin-right: 15px;
    }

    .table__product-action {
        display: flex;
        align-items: center;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #626F6A;

        svg {
            margin-left: 5px;
        }

        &.cover,
        &.cover-second {
            display: none;
        }
    }

    .table__product-item {
        overflow: hidden;

        &:first-of-type {
            @media (max-width: 768px) {
                top: 0;
            }
        }

        &:first-child {
            border-radius: 20px 0 0 20px;
        }

        &:not(:first-child) {
            height: 60px;
        }    
        
        &.uncovered {
            position: relative;

            .cover-first,
            .cover-second,
            .cover {
                display: flex;
            }

            .uncover-first,
            .uncover-second,
            .uncover {
                display: none;
            }
        }
    }

    .table__warehouse {
        &:nth-child(2) {
            .table__warehouse-item-last {
                border-radius: 20px 0 0 20px;
                &::before {
                    display: none;
                }
            }
        }

        &:last-child {
            .table__warehouse-item-last {
                border-radius: 0 20px 20px 0;
            }
        }

        
    }

    .table__warehouse-item {
        display: flex;
        cursor: auto;
        justify-content: center;
        align-items: center;
        position: relative;        

        .filter__checkbox-label {
            margin-bottom: 0;

            .filter__checkbox-name {
                width: 100%;

                &::before,
                &::after {
                    left: 50%;
                    transform: translate(-50%, -50%);
                }

                &::before {
                    background: none;
                }
            }
        }

        &:first-of-type {
            &::before {
                width: 10px;
            }
        }

        &:last-of-type {
            height: 38px;
            margin-bottom: 16px;
            background-color: var(--light-green);
            border-radius: 0;

            &::before {
                content: '';
                position: absolute;
                width: 30px;
                height: 38px;
                background-color: var(--light-green);
                left: -10px;
            }
        }
    }

    .table__warehouse-item-last {
        .filter__checkbox-name {
            &::before {
                border: 2px solid #B9C1BE;
            }
        }
    }

    .table__warehouse-unavailable {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .table__warehouse-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
    }

    .table__warehouse-value {
        flex: 1 1 50%;
        position: relative;

        &:first-child {
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                right: 0;
                width: 1px;
                height: 27px;
                background: #143127;
                transform: translate(0, -50%);
            }
        }
    }
}

.table__product-sublist {
    margin-bottom: 10px;

    .table__product-sublist {
        .table__product-item {
    
            &::before {
                background: #BDDEDA;
            }
        }
    }

    .table__product-item {
        background: #fff;
        height: 60px;
        width: 100%;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 90px;
            height: 100%;
            background: #64B6AC;
        }

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 60px;
            width: 40px;
            height: 100%;
            background: #fff;
            border-radius: 20px 0 0 20px;
        }

        &:first-of-type {
            height: 60px;
            //width: 1070px;    
            border-radius: 20px;   
            margin-bottom: 10px;     
        }
    }    
}

.table__warehouse-sublist {
    margin-bottom: 10px;

    .table__warehouse-item {
        background: #fff;
        height: 60px;
        width: 100%;
        position: relative;

        &.pink {
            background: var(--pink);
        }

        &:first-of-type {
            height: 60px;
            border-radius: 20px;
            background: none;
            margin-bottom: 10px;

            &.pink {
                background: var(--pink);
            }

            &::before {
                display: none;
            }
        }

        &:last-of-type {
            height: 60px;
            border-radius: 20px;
            background: none;
            margin-bottom: 10px;

            &.pink {
                background: var(--pink);
            }

            &::before {
                display: none;
            }
        }
    }
}

.hidden {
    display: none;
}