.content__table-add-resource {
    width: 100%;

    .table__product {
        min-width: 727px;
    }

    .table__product-id {
        width: 140px;
    }

    .table__product-title {
        width: 350px;
    }

    .table__product-value {
        width: 125px;
    }

    .table__product-item {
        &:not(:first-child) {
            height: 50px;
        }        
        justify-content: center;
    }
}

.special-table__product {
    min-width: 710px;
    display: flex;
    flex-direction: column;

    .content__btn-right {
        flex: 1 1 auto;

        .content__btn {
            align-self: flex-end;
        }        
    }

    .controls {
        padding: 40px 24px 0;
    }

    .controls__wrapper {
        justify-content: center;
        
        @media (max-width: 768px) {
            flex-wrap: nowrap;

            .pagination {
                order: 1;
                width: unset;
            }

            .show {
                order: 2;
            }
        }
    }

    .table__product-item {
        &:not(:first-child) {
            height: 50px;
        }

        &:last-child {
            .table__product-titlefull {
                bottom: 20px;
            }
        }

        &.red {
            background-color: var(--pink);
        }
    }

    .table__product-amount {
        width: 27px;
        margin-right: 15px;
    }

    .table__product-art {
        width: 110px;
        margin-right: 15px;
    }

    .table__product-title {
        width: 426px;
        margin-right: 15px;
    }

    .table__product-price {
        width: 31px;
        margin-right: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;

        .filter__checkbox-label {
            margin-bottom: 0;
        }

        .filter__checkbox-name {
            &::before,
            &::after {
                left: 50%;
                transform: translate(-50%, -50%);
            }

            &::before {
                background-color: #fff;
                border: 2px solid #64B6AC;
            }
        }
    }

    .table__product-availability {
        width: 50px;
        margin-right: 15px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;

        .filter__checkbox-label {
            margin-bottom: 0;
        }

        .filter__checkbox-name {
            &::before,
            &::after {
                left: 50%;
                transform: translate(-50%, -50%);
            }

            &::before {
                background-color: #fff;
                border: 2px solid #64B6AC;
            }
        }
    }
}

.table__resources-extra {
    max-width: 396px;
    margin-top: -46px;

    .content__subtitle {
        margin-top: 0;
        margin-bottom: 21px;
    }
}

.table__resources-spec {
    margin-top: -46px;

    .content__subtitle {
        margin-top: 0;
        margin-bottom: 21px;
    }
}

.content__table-resources {
    .table__wrapp {
        padding-top: 50px;
    }
}

.special-table {

    &__header {
        margin: 10px 0 15px;
        display: flex;
        align-items: center;

        .content__subtitle {
            margin-top: 0;
            flex: 1 1 auto;
        }
    }

    &__search {
        position: relative;
        margin: 0 10px;
        width: 400px;
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        overflow: hidden;
        padding: 0 50px;
        display: none;
        align-items: center;
        justify-content: center;

        &.active {
            display: flex;
        }

        input {
            height: 100%;
            background: none;
        }

        &-btn {
            position: absolute;
            left: 13px;

            svg {
                width: 24px;
                height: 24px;
            }
        }

        &-close {
            position: absolute;
            right: 13px;            

            svg {
                width: 24px;
                height: 24px;
                stroke: #BDDEDA;
            }
        }

        &-show {
            display: none;
            position: relative;
            left: 0;
            width: 70px;
            height: 44px;
            border: 2px solid #BDDEDA;
            border-radius: 40px;
            margin-right: 10px;

            &.active {
                display: block;
            }
        }
    }    
}

.special-content {
    margin-top: 40px;

    &__table {
        .table__wrapp {
            grid-gap: 40px;

            @media (max-width: 993px) {
                flex-direction: column;
            }
        }
    }
}
