.analytics {
    flex: 1 1 auto;
    background: #F5F5F5;

    .download__select-item {
        justify-content: flex-start;
        padding: 0 20px;
    }
}

.content-analytics {
    padding: 48px 24px 40px;

    @media (max-width: 768px) {
        padding: 24px 16px 40px;
    }

    @media (max-width: 480px) {
        padding: 16px 14px 40px;
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        flex-wrap: wrap;
        row-gap: 24px;

        @media (max-width: 768px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__title {
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 30px;
        line-height: 38px;
        margin-right: 10px;
        color: #143127;

        @media (max-width: 768px) {
            font-size: 24px;
            line-height: 30px;
        }

        @media (max-width: 480px) {
            font-size: 20px;
            line-height: 25px;
        }
    }

    .navigation__filter-btn {
        @media screen and (min-width: 600px) {
            visibility: hidden;
            position: absolute;
        }
    }

    &__options-body {
        margin: 0 0 0 auto;

        @media (max-width: 1240px) {
            width: 100%;
        }        
    }

    &__options {
        justify-content: flex-end;
        gap: 10px;
        flex: 1 1 auto;

        @media (max-width: 600px) {
            margin-top: 14px;
        }

        @media screen and (min-width: 600px) {
            display: flex;
        }

        @media (max-width: 1240px) {
            flex-wrap: wrap;

            .content-analytics__select {
                flex: 1 0 48%;
                width: 100%;
            }
    
            .content-analytics__date {
                flex: 1 0 48%;
            }

            .select-charts__btn {
                width: 100%;
            }

            .date-analytics__btn {
                width: 100%;

                @media (max-width: 768px) {
                    margin-top: 0;
                }
            }
        }     
    }

    &__btns {
        margin-top: 7px;
        display: flex;
        justify-content: flex-end;

        @media (max-width: 768px) {
            justify-content: flex-start;
        }

    }

    &__export {
        display: flex;
        align-items: center;   

        svg {
            margin-right: 12px;
        }
    }
}

.table-analytics {    
    width: 100%;
    overflow-x: auto;
    padding-bottom: 13px;

    &__list {
        min-width: 1700px;
    }

    &__row {
        padding: 0 20px;
    }

    &__row-header {
        background: #BDDEDA;
        border-radius: 20px;
        height: 30px;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .table-analytics__item {
            text-align: center;
        }
    }

    &__row-subheader,
    &__row-footer {
        background: #DAEAE8;
        border-radius: 20px;
        height: 60px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__row-subheader {
        .table-analytics__percent {
            color: #64B6AC;
        }
        .table-analytics__percent-minus {
            color: #EB6767;
        }
    }

    &__row-footer {
        margin-top: 20px;

        .table-analytics__percent {
            color: #64B6AC;
        }

        .table-analytics__percent-minus {
            color: #EB6767;
        }
    }

    &__row-type {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #143127;
        height: 48px;
        padding: 10px 20px;
        display: flex;
        align-items: flex-end;
    }

    &__row-data,
    &__row-data-first,
    &__row-data-last,
    &__row-data-single {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 60px;
        background: #fff;

        .table-analytics__percent {
            &::before {
                background-color: #EB6767;
            }
        }        
    }

    &__row-data-first {
        border-radius: 20px 20px 0px 0px;
    }

    &__row-data-last {
        border-radius: 0px 0px 20px 20px;
    }

    &__row-data-single {
        border-radius: 20px 20px 20px 20px;
    }

    &__item {
        min-width: 170px;
        font-family: var(--font-family-futura);
        font-weight: 500;
        font-size: 14px;
        line-height: 18px;
        color: #143127;

        &:first-child {
            min-width: 220px;
        }
    }

    &__item-double {
        display: flex;
        align-items: center;
    }

    &__value {
        flex: 0 0 50%;
    }

    &__percent {
        flex: 0 0 50%;
        padding-left: 36px;        
        color: #626F6A;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
            width: 24px;
            height: 24px;
            mask: url('../img/sprite/money.svg') 100% center no-repeat;
            background-color: #64B6AC;
        }
    }

    &__percent-minus {
        color: #EB6767;

        &::before {
            background-color: #EB6767;
        }
    }
}

.date-analytics {

    @media (max-width: 480px) {
        width: 100%;
    }

    &__btn {
        height: 44px;
        width: 292px;
        padding: 0 45px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        display: flex;
        align-items: center;
        position: relative;

        @media (max-width: 768px) {
            margin-top: 10px;
        }

        @media (max-width: 480px) {
            width: 100%;
        }

        &::before {
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            left: 15px;
            top: 50%;
            transform: translate(0, -50%);
            mask: url('../img/sprite/calendar.svg') center / cover no-repeat;
            background: #BDDEDA;
        }

        svg {
            fill: #64B6AC;
            transform: rotate(-90deg);
            width: 7px;
            height: 14px;
            opacity: 1;
            position: absolute;
            right: 16px;
        }
    }
}

.charts {
    //width: 100%;
    //overflow-x: auto;

    &:not(:last-child) {
        margin-bottom: 40px;
    }

    &__block {
        background: #FFFFFF;
        box-shadow: 0px 4px 15px -5px rgba(213, 213, 213, 0.64);
        border-radius: 20px;
        padding: 40px;            
    }

    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        flex-wrap: wrap;
        row-gap: 15px;

        @media (max-width: 993px) {
            //align-items: flex-start;
            //flex-wrap: wrap;
        }

        @media (max-width: 480px) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__title {
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 18px;
        line-height: 23px;
        color: #143127;
        flex: 1 1 auto;
    }

    &__options {
        display: flex;

        //@media (max-width: 993px) {
        //    flex-direction: column;
        //    align-items: flex-end;
        //    row-gap: 15px;
        //}
    }

    &__bar-horizontal {
        width: 100%;
        overflow-x: auto;        
    }

    &__bar-wrapper {
        min-width: 1792px;
    }

    &__sort {
        display: flex;
        align-items: center;
        gap: 12px;
        margin: 0 10px;

        &.left {
            .charts__sort-sum {
                color: #64B6AC;
            }
            
        }
        &.right {
            .charts__sort-count {
                color: #64B6AC;
            }
            .charts__sort-switcher {
                &::before {
                    left: unset;
                    right: 0;
                }
            }
        }
    }

    &__sort-sum {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 1;
        color: #626F6A;
    }

    &__sort-switcher {
        width: 48px;
        height: 11px;
        background: #BDDEDA;
        border-radius: 100px;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            width: 22px;
            height: 22px;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
            border-radius: 50%;
            background: #64B6AC;
        }
    }

    &__sort-count {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 1;
        color: #626F6A;
    }

    &__select-wrapper {
        display: flex;
        gap: 10px;

        @media (max-width: 993px) {
            flex: 0 0 100%;
            justify-content: flex-end;
            flex-wrap: wrap;
        }

        @media (max-width: 480px) {
            width: 100%;
        }
    }
}

.select-charts {
    position: relative;

    @media (max-width: 600px) {
        margin-bottom: 10px;
    }

    &.active {
        .select-charts__menu {
            display: block;
        }
    }

    @media (max-width: 768px) {
        flex: 1 0 270px;
    }

    &__btn {
        width: 285px;
        height: 44px;
        border: 2px solid #BDDEDA;
        border-radius: 40px;
        display: flex;
        align-items: center;
        padding: 0 45px;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #143127;
        position: relative;
        white-space: nowrap;

        @media (max-width: 768px) {
            width: 100%;
        }

        &--groups {
            &::before {
                mask: url('../img/sprite/folder-open.svg') center / cover no-repeat;
            }
        }

        &--categories {
            &::before {
                mask: url('../img/sprite/categories.svg') center / cover no-repeat;
            }
        }

        &--stores {
            &::before {
                mask: url('../img/sprite/building.svg') center / cover no-repeat;
            }
        }

        &--crowns {
            &::before {
                mask: url('../img/sprite/crown.svg') center / cover no-repeat;
            }
        }

        &--sources {
            &::before {
                mask: url('../img/sprite/source.svg') center / cover no-repeat;
            }
        }

        &::before {
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            left: 15px;
            top: 50%;
            transform: translate(0, -50%);            
            background: #BDDEDA;
        }

        svg {
            fill: #64B6AC;
            transform: rotate(-90deg);
            width: 7px;
            height: 14px;
            opacity: 1;
            position: absolute;
            right: 16px;
        }
    }

    &__selected {
        margin: 0 3px;
    }

    &__from {
        margin: 0 3px;
    }

    &__menu {
        display: none;
        background: #FFFFFF;
        box-shadow: 0px 4px 15px rgba(212, 212, 212, 0.64);
        border-radius: 12px;
        padding: 16px;
        position: absolute;
        top: 100%;
        width: 100%;
        z-index: 1;
    }

    &__list {
    }

    &__item {
    }

    &__checkbox-label {
        height: 100%;
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 12px;
    }

    &__checkbox-input {
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 0.3s ease;

        &:checked ~ .select-charts__checkbox-name::after{
        opacity: 1;
        }

        &:checked ~ .select-charts__checkbox-name::before{
        background-color: var(--white);
        border: none;
        }
    }

    &__checkbox-name {
        position: relative;
        padding-left: 34px;
        font-size: 18px;
        color: var(--gray);
        transition: all 0.3s ease;
        cursor: pointer;
        white-space: nowrap;

        &::before {
            content: "";
            position: absolute;
            display: block;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: var(--white);
            width: 24px;
            height: 24px;
            border: 2px solid var(--light-green);
            border-radius: 6px;
            transition: all .3s ease;
            z-index: 1;
        }

        &::after {
            content: "";
            position: absolute;
            display: block;
            background-image: var(--arrow-check);
            background-size: cover;
            background-repeat: no-repeat;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            z-index: 5;
            opacity: 0;
            transition: all 0.3s ease;
        }

        &:hover {
            transition: all 0.3s ease;

            &::before {
                background-color: var(--light-green);
            }
        }

        @media (max-width: 768px) {
            font-size: 16px;
        }
    }
}

.main-analytics {

    &__cards {
        display: grid;
        gap: 24px;
        grid-template-columns: repeat(4, 2fr) 1.5fr;

        @media (max-width: 1550px) {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(5, 1fr);
            grid-auto-flow: column;
        }

        @media (max-width: 768px) {
            gap: 16px;
        }

        @media (max-width: 600px) {
            grid-auto-flow: row;
            grid-template-columns: 1fr;
            grid-template-rows: auto;
        }
    }

    &__subcards {
        display: flex;
        margin: 0 -12px;

        @media (max-width: 993px) {
            flex-direction: column;
        }
    }
}

.card-analytics {
    padding: 24px;
    background: #64B6AC;
    border-radius: 20px;
    position: relative;

    &.secondary {
        background: #626F6A;

        .card-analytics__value-extra {
            color: rgba(255, 255, 255, 0.6);
        }
    }

    &-last {
        display: flex;
        margin: 0 -5px;

        .card-analytics__percent {
            position: relative;
            bottom: 0;
            right: 0;
        }

        @media (max-width: 1550px) {
            margin: 0;
        }
        
    }

    &__left,
    &__right {
        flex: 0 0 50%;
        padding: 0 5px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;

        .card-analytics__title {
            margin: 0;
        }

        .card-analytics__value {
            flex: 1 1 auto;
            height: unset;
        }
    }

    &__left {
        .card-analytics__value-main {
            color: #FDC300;
        }
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 14px;
    }

    &__label {
        flex: 0 0 61px;
        width: 61px;
        height: 61px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #FDC300;

        svg {
            width: 33px;
            height: 33px;
        }

        @media (max-width: 993px) {
            flex: 0 0 40px;
            width: 40px;
            height: 40px;

            svg {
                width: 22px;
                height: 22px;
            }
        }

        @media (max-width: 480px) {
            display: none;
        }
    }

    &__title {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 20px;
        line-height: 120%;
        color: #FFFFFF;
        margin: 0 12px;
        flex: 1 1 auto;

        @media (max-width: 768px) {
            font-size: 16px;
        }

        @media (max-width: 480px) {
            margin: 0;            
        }
    }

    &__percent {
        background: rgba(255, 255, 255, 0.2);
        border-radius: 100px;
        padding: 5px 10px;
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;

        @media (max-width: 1680px) {
            position: absolute;
            right: 24px;
            bottom: 44%;
        }

        @media (max-width: 1550px) {
            position: relative;
            right: 0;
        }

        @media (max-width: 768px) {
            position: absolute;
            right: 24px;
            bottom: 49%;
        }

        @media (max-width: 600px) {
            position: relative;
            right: 0;
            bottom: 0;
            font-size: 14px;
            line-height: 17px;
            padding: 3px 7px;
        }
    }

    &__info {
        display: flex;
    }

    &__value {
        flex: 0 0 100%;
        display: flex;
        flex-direction: column;
        height: 86px;
        position: relative;
        z-index: 1;

        @media (max-width: 1680px) {
            height: 130px;
        }

        @media (max-width: 1550px) {
            height: 86px;
        }

        @media (max-width: 768px) {
            height: 130px;
        }

        @media (max-width: 600px) {
            height: 86px;            
        }
    }

    &__value-main {
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 40px;
        line-height: 51px;
        color: #FFFFFF;

        @media (max-width: 600px) {
            font-size: 22px;
            line-height: 28px;
        }
    }

    &__value-extra {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: #BDDEDA;

        @media (max-width: 600px) {
            font-size: 16px;
            line-height: 19px;
        }
    }

    &__graph {
        width: 165px;
        height: 100px;
        position: absolute;
        bottom: 40px;
        right: 15px;
    }
}

.subcard-analytics {
