}



.charts-tooltip {
    max-width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 14px;
    position: relative;

    &__name {
        font-family: var(--font-family-futura);
        font-weight: 400;
        font-size: 14px;
        line-height: 12px;
        color: #626F6A;
        margin-bottom: 5px;
    }

    &__value {
        font-family: var(--font-family-futura);
        font-weight: 700;
        font-size: 22px;
        line-height: 28px;
        color: #143127;
    }
}

#cat-chart {
    margin-left: -11px;
}